export const GetJobCount = () =>{
    return GetJobs().length;
}

export const GetDepartments = () => {
    let res = ['Art', 'Programming', "QA", "Human Resources"];

    return res;
}

export const GetEmploymentStatuses = () => {
    let res = ["Full Time", "Part Time", "Co-op", "Contractor"];

    return res;
}

export const GetJob = (jobId) => {
    let id = parseInt(jobId);
    return GetJobs().filter((job) => (job.id === id))[0];
}